import { Injectable, OnInit } from "@angular/core";
import { Http, Response } from "@angular/http";
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';

@Injectable()
export class EventService implements OnInit {

    private tempUrl = "http://localhost:54110/api/values";
    private tempVar: string = "null";
    private handleError: any;

    constructor(private _http: Http)
    {

        this.getValues().subscribe(data => this.tempVar = data);
        console.log(this.tempVar);
    } 

    getValues() {
        return this._http.get(this.tempUrl).map((res: Response) => res.json());
    }

    getEvents() {
        return EVENTS;
    }

    ngOnInit()
    {
        console.log("Init")
        console.log(this.getValues());
        console.log(this.tempVar);
    }
}

const EVENTS = [
    {
        id: 1,
        name: 'Pudge',
        date: 'Early Mordem',
        time: '8:00 am',
        price: '100 gold',
        imageUrl: 'app/assets/images/gg.png',
        location:
        {
            address: 'Mid Point',
            city: 'Dire',
            country: 'Azengard'
        }
    },
    {
        id: 2,
        name: 'Mirana',
        date: 'Late Mordem',
        time: 'Midnight',
        price: '625 gold',
        imageUrl: 'app/assets/images/gg.png',
        onlineUrl: "blog.dota2.com"
    },

]


